import { tr } from "@/services/lang";
export default {
  props : ['product'],
  computed: {
    lang() {
      return tr();
    },
  },
  data() {
    return {
      is_hover: false,
    };
  },
  methods: {
    item_hover() {
      if (window.innerWidth > 768) {
        this.is_hover = true;
      }
    },
    detail_item() {
     this.$router.push(`products/${554}`)
    },
    add_item(){
        this.$store.dispatch("products/addProductToCart",{id:4,name:'کت',price:100000,img:''});
    }
  },
};
